import React, { Fragment, useEffect, useState, useRef } from 'react'
import {
    ChevronDown, Share, Printer, FileText, File, Grid, CheckCircle, Plus, MoreVertical, Edit, Archive, Trash, Search, Eye, Lock, ChevronLeft, RefreshCw
} from 'react-feather'
import {
    Card, CardHeader, CardTitle, Button, UncontrolledButtonDropdown, DropdownToggle, DropdownMenu, DropdownItem, Input, Label, Row, Col, Badge, Form, FormGroup, UncontrolledDropdown, Spinner, CardBody, TabContent, TabPane, Nav, NavItem, NavLink, InputGroup, InputGroupAddon, InputGroupText, CustomInput
} from 'reactstrap'
import Select from 'react-select'
import { selectThemeColors, transformInToFormObject } from '@utils'
import { useHistory } from 'react-router-dom'
import useJwt from '@src/auth/jwt/useJwt'
import { toast } from 'react-toastify'
import { CURRENCY_SYMBOL } from '../../../Configuarables'
import { Error, Success, ErrorMessage } from '../../../viewhelper'
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
const MySwal = withReactContent(Swal)
import CommonDataTable from '../../CommonDataTable'

const TenantList = () => {
    const userRole = JSON.parse(localStorage.getItem('userData')).role
    const history = useHistory()
    const [TableDataLoading, setTableDataLoading] = useState(true)
    const [TenantList, setTenantList] = useState([])
    const [resetData, setReset] = useState(false)

    useEffect(() => {
        useJwt.TenantList().then(res => {
            const responseData = res.data.payload
            console.log(responseData)
            setTenantList(responseData)
            setTableDataLoading(false)
        }).catch(err => {
            Error(err)
            console.log(err)
            setTableDataLoading(false)
        })
    }, [resetData])
    const handlePoPupActions = (renter_id, message) => {
        return MySwal.fire({
            title: message,
            text: `You won't be able to revert this`,
            icon: 'warning',
            allowOutsideClick: false,
            allowEscapeKey: false,
            showCancelButton: true,
            confirmButtonText: 'Yes',
            customClass: {
                confirmButton: 'btn btn-primary',
                cancelButton: 'btn btn-danger ml-1'
            },
            showLoaderOnConfirm: true,
            preConfirm: () => {
                // console.log({ land_lord_id })
                return useJwt.deleteTenant({ renter_id }).then(res => {
                    Success(res)
                    console.log(res)
                    setTenantList(TenantList.filter(x => x.user_id !== renter_id))
                }).catch(err => {
                    console.log(err)
                    Error(err)
                })
            },
            buttonsStyling: false,
            allowOutsideClick: () => !Swal.isLoading()
        }).then(function (result) {
            if (result.isConfirmed) {
            }
        })
    }
    const column = [
        {
            name: 'Photo',
            maxWidth: '100px',
            // sortable: true,
            selector: row => <img src={row.photo} alt="photo" width="50px" />
        },
        {
            name: 'Name',
            minWidth: '100px',
            sortable: true,
            selector: 'full_name'
        },
        // {
        //     name: 'Type',
        //     minWidth: '100px',
        //     sortable: true,
        //     selector: row => row.Tenant_link.Tenant_type.type_name
        // },
        {
            name: 'Email',
            minWidth: '150px',
            // sortable: true,
            selector: 'email'
        },
        {
            name: 'Mobile',
            minWidth: '100px',
            sortable: true,
            selector: 'mobile'
        },
        {
            name: 'Address',
            minWidth: '300px',
            // sortable: true,
            selector: 'address'
        },
        {
            name: 'Action',
            minWidth: '100px',
            // sortable: true,
            selector: row => {
                return <>
                    <span title="Edit">
                        <Edit size={15}
                            color='teal'
                            style={{ cursor: 'pointer' }}
                            onClick={(e) => {
                                localStorage.setItem('EditTenantInfo', JSON.stringify(row))
                                history.push(`/${userRole}/EditTenant`)
                            }}
                        />
                    </span>&nbsp;&nbsp;
                    <span title="Delete">
                        <Trash size={15}
                            color='red'
                            style={{ cursor: 'pointer' }}
                            onClick={(e) => handlePoPupActions(row.user_id, 'Do you want to delete?')}
                        />
                    </span>
                </>
            }
        }
    ]
    return (
        <Card>
            <CardHeader className='border-bottom titleSelect'>
                <CardTitle tag='h4'>Tenant list</CardTitle>
            </CardHeader>
            <CardBody>
                <Row>
                    <Col md='12'>
                        <CommonDataTable column={column} TableData={TenantList} TableDataLoading={TableDataLoading} />
                    </Col>
                </Row>
            </CardBody>
        </Card>
    )
}

export default TenantList